export default {
	name:"trailerStars",
	data() {
		return {
			yellowScore:0,
			greyScore:5
		};
	},
	props:{
		innerScore:0, // 定义外部传入的分数值
		showNum:0  // 是否显示分数值  1:显示，0:不显示
	},
	created() {
		var tempScore = 0;
		if(this.innerScore){
			tempScore = this.innerScore;
		}
		var yellowScore = parseInt(tempScore/2)
		this.greyScore = 5 - yellowScore
		this.yellowScore = yellowScore
	}
}
